*{
  margin:0;
  padding:0; 
  box-sizing: border-box;
  list-style: none; 
  text-decoration: none;
  list-style-type: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  font-family: 'Lato',
   sans-serif; font-family: 'Noto Sans KR',
   sans-serif;
}
p,a,h1,h2,h3,h4,h5,span{
  color: #333;
}

/* header 영역입니다. */
#contents{
  position: relative;
  margin: 0 auto; 
  width: 100%;
  padding: 0 15px;
}
.navbar{
  display: none;
}
#header{
  width: 100%;
  position: fixed;
  line-height: 40px;
  left:0;
  top: 0;
  display: inline-block;
  z-index: 100;
  padding: 10px 0;
  text-align: left;
  background: #fff;
}
#logo{
  font-size: 20px;
  position:relative;
  left: 15px;
}
#openMenu{
  position: absolute;
  right: 15px; 
  top: 10px;
  font-size: 22px; 
  z-index: 99;
  transition: all .5s ease;
  -webkit-transition: all .5s ease;
}
#sideMenu{

  width: 100%;
  z-index: 2;
  height: 100vh;
  position: fixed;
  /* right: -3000px; */
  top: 0;
  height: 100%;  
  background:#f33;
  transition: all .5s ease;
  -webkit-transition: all .5s ease;

}
.list-wrap{
  margin-top: 100px;
  transition: all 1s;
}
.list-menu{
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  
  /* right: -300px; */

}
.list-menu li{
  padding: 10px 0;
}


/* header 영역입니다. */

/* footer 영역입니다. */

#footer{
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  font-size: 12px;
  text-align: left;
}
#footer .footer-wrap{
  width: 50%;
}
#footer a{
  text-decoration: underline;
}
/* footer 영역입니다. */


@media(min-width:980px){

  .menu-wrap{
    display: none;
  }
  #logo{
    font-size: 30px;
  }
  .navbar{
    display: inline-block;
    position: absolute;
    right: 15px;
    top: 27px;
  }
  .navbar ul li {
    float: left;
    padding-left: 50px;
  }
  #footer{
    font-size: 15px;
  }
  #footer .footer-wrap{
    width: 30%;
  }
}

#section {
  margin: 100px 0;
}
#section .main-box-wrap{
  display: flex;
  flex-direction: column;
}
#section .main-box-wrap .main-box{
  width: 100%;
  margin-bottom: 20px;
}
#section .main-box-wrap .main-box img{
  width: 100%;
}
#section .main-box-wrap .main-box .main-text{
  font-weight: 400;
  text-align: center;
}

@media(min-width:820px){
  #section .main-box-wrap{
    flex-direction: row;
  }
  #logo{
    padding: 15px 0;
  }
  #section .main-box-wrap .main-box{
    position: relative;
    width: 50%;
    padding: 10px;
    margin: 0;
  }
  #section .main-box-wrap .main-box:nth-child(1){
    padding-left: 0;
  }
  #section .main-box-wrap .main-box:nth-child(2){
    padding-right: 0;
  }
  #section .main-box-wrap .main-box .main-text{
    position: absolute;
    top: 50%;
    transform: translateX(-50%); left: 50%;
    display: none;
    font-size: 35px;
    font-weight: 600;
  }
  #section .main-box:hover .main-text{
    color:#000;
    display: block;
    transition: all 1s ease;
    
  }
  #section .main-box img:hover{
    transition: opacity .2s ease-in-out;
    opacity: 0.2;
  }
}
